.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 30px;
  display: flex;
}

.photo-card {
  width: 300px;
  border: 2px solid gray;
  border-radius: 10px;
}

.card-img {
  width: 300px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.info {
  flex-direction: column;
  display: flex;
}

.load-more-hidden {
  display: none;
}

.load-more {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1.2em;
  font-weight: bold;
  transition: background-color .3s ease-in-out, transform .2s ease-in-out;
  display: inline-block;
}

.btn-container {
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 30px;
  display: flex;
}

